import React from "react";
import { DisplayButton } from "@helsenorge/toolkit/components/atoms/buttons/display-button";
import { Link } from "react-router-dom";

const changeGoalsContent = () => {
  return (
    <div>
      <div style={{ color: "black" }}>
        Om du har behov for  justere mlene dine kan du endre p dem.
      </div>
      <br />
      <div className="dashboard-button">
        <Link to={"/mygoals"} style={{ borderBottom: "none" }}>
          <DisplayButton secondary>
            <div className={"dashboard-button-sizing"}>G til Mine ml </div>
          </DisplayButton>
        </Link>
      </div>
    </div>
  );
};
export default changeGoalsContent;
